import * as styles from "../../../styles/restaurant.module.css";
import React, { useEffect, useState } from 'react';
import MediaQuery from 'react-responsive';
import { useDispatch, useSelector } from 'react-redux';
import { setHasOpened, setOpenedMenu } from '../../../state/menuSlice';

const MenuButtons = (props) => {
    const dispatch = useDispatch();
    const restaurant = useSelector((state) => state.restaurant);
    const [menuIndexArray, setMenuIndexArray] = useState([]);
    const [buttonsArray, setButtonsArray] = useState([]); // Store filtered menus here

    useEffect(() => {
        // Filter menus and populate menuIndexArray
        const tempMenuIndexArray = [];
        const tempButtonsArray = restaurant.menus.filter((value, index) => {
            const menuName = value[0][0].menu.toLowerCase();
            const isTop = props.topbottom === 'top' &&
                !menuName.includes('drink') &&
                !menuName.includes('dess') &&
                !menuName.includes('bever') &&
                !menuName.includes('sweet');
            const isBottom = props.topbottom === 'bottom' &&
                (menuName.includes('drink') ||
                    menuName.includes('dess') ||
                    menuName.includes('bever') ||
                    menuName.includes('sweet'));

            if (isTop || isBottom) {
                tempMenuIndexArray.push(index);
                return true;
            }
            return false;
        });

        setMenuIndexArray(tempMenuIndexArray);
        setButtonsArray(tempButtonsArray);
    }, [restaurant.menus, props.topbottom]); // Re-run when menus or props change

    useEffect(() => {
        // Adjust button heights
        const adjustHeights = () => {
            buttonsArray.forEach((menu, index) => {
                if (index % 2 !== 0) { // right button
                    const rButton = document.getElementsByClassName("mobile_menu_button")[index];
                    const lButton = document.getElementsByClassName("mobile_menu_button")[index - 1];

                    if (rButton && lButton) {
                        const rHeight = window.getComputedStyle(rButton, null).getPropertyValue("height");
                        const lHeight = window.getComputedStyle(lButton, null).getPropertyValue("height");

                        if (parseFloat(rHeight) > parseFloat(lHeight)) {
                            lButton.setAttribute("style", `height: ${rHeight}`);
                        } else {
                            rButton.setAttribute("style", `height: ${lHeight}`);
                        }
                    }
                }
            });
        };

        setTimeout(adjustHeights, 0); // Wait for DOM update
    }, [buttonsArray]);

    const setBrowsingWindowPosition = () => {
        dispatch(setHasOpened());
        window.scrollTo(0, 0);
    };

    return (
        <div className={styles.mobile_menu_buttons}>
            {buttonsArray.map((menuObject, index) => {
                const currentMenu = menuObject[0][0].menu;

                if (!buttonsArray[index + 1] && index % 2 === 0) {
                    return (
                        <div
                            key={index}
                            className={[styles.button_menu, styles.button_menu_center].join(" ")}
                            onClick={() => {
                                dispatch(setOpenedMenu(menuIndexArray[index]));
                                setBrowsingWindowPosition();
                            }}
                            key={index}
                        >
                            <p>{currentMenu}</p>
                        </div>
                    );
                } else {
                    return (
                        <div
                            key={index}
                            className={[styles.button_menu, styles.mobile_menu_button].join(" ")}
                            onClick={() => {
                                dispatch(setOpenedMenu(menuIndexArray[index]));
                                setBrowsingWindowPosition();
                            }}
                            key={index}
                        >
                            <p>{currentMenu}</p>
                        </div>
                    );
                }
            })}
        </div>
    );
};

export default MenuButtons;
