import * as styles from "../../../styles/restaurant.module.css";
import React, { Component } from 'react'
import { Link } from 'gatsby'
import MediaQuery from 'react-responsive'
import MenuButtons from './MenuButtons'
import { useDispatch, useSelector } from "react-redux";
import { toggleView } from "../../../state/menuSlice";

const MenuHeader = (props) => {

    const dispatch = useDispatch()
    const restaurant = useSelector((state) => state.restaurant);

    const menuToggle = () => {
        dispatch(toggleView())
    }

    return (
            <div>
            <MediaQuery maxWidth={767}>
                <div>
                    <div onClick={() => menuToggle()}>
                        <h1 className={[styles.menu_header, styles.mobile_menu_header].join(" ")}>{restaurant.restaurantInfo.map((tableline, index) => {
                            if( tableline[0] == 'name' ) {
                                return (
                                    <div key={index} className={styles.link}>
                                        {tableline[1]}
                                    </div>
                                )
                            }
                        })}</h1>
                    </div>

                </div>
            </MediaQuery>

            <MediaQuery minWidth={768} maxWidth={991}>
                <div>
                    <div onClick={() => menuToggle()}>
                        <h1 className={[styles.menu_header, styles.tablet_menu_header].join(" ")}>{restaurant.restaurantInfo.map((tableline, index) => {
                            if( tableline[0] == 'name' ) {
                                return (
                                    <div key={index} className={styles.link}>
                                        {tableline[1]}
                                    </div>
                                )
                            }
                        })}</h1>
                    </div>

                    <MenuButtons {...props} />

                </div>
            </MediaQuery>

            <MediaQuery minWidth={992}>
                <div>
                    <div onClick={() => menuToggle()}>
                        <h1 className={styles.menu_header}>{restaurant.restaurantInfo.map((tableline, index) => {
                            if( tableline[0] == 'name' ) {
                                return (
                                    <div key={index} className={styles.link}>
                                        {tableline[1]}
                                    </div>
                                )
                            }
                        })}</h1>
                    </div>

                    <MenuButtons {...props} />

                </div>
            </MediaQuery>
            </div>
        )
}

export default MenuHeader
