import * as styles from "../../styles/restaurant.module.css";
import React, { useEffect } from "react";
import RestaurantHome from "./dumb/RestaurantHome";
import MenuHome from "./dumb/MenuHome";
import Loading from "./dumb/Loading";
import MediaQuery from "react-responsive";
import { useDispatch, useSelector } from "react-redux";
import { setInfoData, setHoursData, setMenuData } from "../../state/menuSlice";
import { useMenu, useKeyValue, useKeyValueArray } from "../spreadsheet/useSheet";
const config = require("../../../config");

const Menu = () => {
    const dispatch = useDispatch();
    const showMenu = useSelector((state) => state.restaurant.showMenu);

    // Fetch `info` sheet data
    const {
        loading: infoLoading,
        error: infoError,
        data: infoData,
    } = useKeyValue({
        spreadsheetId: config.spreadsheets.test.id,
        sheetName: "info",
    });

    // Fetch `hours` sheet data
    const {
        loading: hoursLoading,
        error: hoursError,
        data: hoursData,
    } = useKeyValueArray({
        spreadsheetId: config.spreadsheets.test.id,
        sheetName: "hours",
    });

    // Fetch `menu` sheet data
    const {
        loading: menuLoading,
        error: menuError,
        data: menuData,
    } = useMenu({
        spreadsheetId: config.spreadsheets.test.id,
        sheetName: "menu",
    });

    const loading = infoLoading || hoursLoading || menuLoading;

    useEffect(() => {
        if (!infoLoading && !infoError) {
            dispatch(setInfoData(infoData));
        }

        if (!hoursLoading && !hoursError) {
            dispatch(setHoursData(hoursData));
        }

        if (!menuLoading && !menuError) {
            dispatch(setMenuData(menuData));
        }
    }, [dispatch, infoLoading, infoError, infoData, hoursLoading, hoursError, hoursData, menuLoading, menuError, menuData]);

    return (
        <div>
            {loading ? (
                <Loading />
            ) : (
                <div>
                    <MainContent showMenu={showMenu} />
                </div>
            )}
        </div>
    );
};

const MainContent = ({ showMenu }) => {
    return (
        <div>
            <MediaQuery maxWidth={767}>
                <div className={styles.mobile_restaurant}>
                    {showMenu ? <MenuHome /> : <RestaurantHome />}
                </div>
            </MediaQuery>

            <MediaQuery minWidth={768} maxWidth={991}>
                <div className={styles.mobile_restaurant}>
                    {showMenu ? <MenuHome /> : <RestaurantHome />}
                </div>
            </MediaQuery>

            <MediaQuery minWidth={992}>
                <div className={styles.restaurant}>
                    {showMenu ? <MenuHome /> : <RestaurantHome />}
                </div>
            </MediaQuery>
        </div>
    );
};

export default Menu;
