import * as styles from "../../../styles/restaurant.module.css";
import * as otherstyles from "../../../styles/index.module.css";
import React, { useEffect } from "react";
import ViewSortedMenuList from "./ViewSortedMenuList";
import MenuHeader from "./MenuHeader";
import MenuSection from "./MenuSection";
import Footer from "../../footer";
import MenuButtons from "./MenuButtons";
import { useDispatch, useSelector } from "react-redux";

const MenuHome = (props) => {
    const hasOpened = useSelector((state) => state.restaurant.hasOpened);

    return (
        <div className={otherstyles.container}>
            <MenuHeader className={styles.menu_block} />
            <MenuButtons topbottom="top" />
            {hasOpened && <ViewSortedMenuList {...props}/>}
            <MenuButtons topbottom="bottom" />
            <Footer />
        </div>
    );
};

export default MenuHome;
