import * as styles from "../../../styles/restaurant.module.css";
import React from 'react'
import MediaQuery from 'react-responsive'

const Price = (props) => {

    if(isNaN(props.price)) {
        return(
            null
        )
    }
    return (
        <div className={[styles.mobile_menu_item_right, styles.item_lr].join(" ")}>
            <MediaQuery maxWidth={767}>
                <div className={[styles.item_detail, styles.mobile_item_price].join(" ")}>
                    {parseFloat(Math.round((props.price) * 100) / 100).toFixed(2)}
                </div>
            </MediaQuery>

            <MediaQuery minWidth={768} maxWidth={991}>
                <div className={[styles.item_detail, styles.item_price].join(" ")}>
                    {parseFloat(Math.round((props.price) * 100) / 100).toFixed(2)}
                </div>
            </MediaQuery>

            <MediaQuery minWidth={992}>
                <div className={[styles.item_detail, styles.item_price].join(" ")}>
                    {parseFloat(Math.round((props.price) * 100) / 100).toFixed(2)}
                </div>
            </MediaQuery>
        </div>
    )
}
export default Price
