import * as styles from "../../../styles/restaurant.module.css";
import React, { useEffect, useRef, useState } from 'react';
import MenuItem from './MenuItem';
import Comment from './Comment';

const MenuSection = (props) => {
    const { isOpen, onToggle, sectionIndex } = props; // Receive open/close status and toggle function
    const sectionRef = useRef(null); // Create a ref to the section element
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 767); // State to track screen size

    // Handle window resize events
    useEffect(() => {
        const handleResize = () => {
            setIsMobile(window.innerWidth <= 767);
        };

        // Add event listener for resizing
        window.addEventListener('resize', handleResize);

        // Cleanup event listener
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    // Extract the section comment (if any)
    const sectionComment = props.list.find((item) => item.sectioncomment)?.sectioncomment;

    // Scroll the section into view when it is opened
    useEffect(() => {
        if (isOpen && sectionRef.current) {
            sectionRef.current.scrollIntoView({
                behavior: 'smooth',
                block: 'start', // Align the section to the top of the viewport
            });
        }
    }, [isOpen]); // Only run when the isOpen prop changes

    return (
        <div ref={sectionRef}>
            {/* Render section header */}
            <h2
                className={[
                    styles.scrolled_open_section,
                    isMobile
                        ? styles.mobile_section_header
                        : styles.section_header,
                ].join(' ')} // Combine mapped style classes
                onClick={() => onToggle(sectionIndex)} // Call the toggle function on click
            >
                {props.sectionTitle}
            </h2>

            {/* Render section comment (only when the section is open) */}
            {isOpen && sectionComment && <Comment text={sectionComment} />}

            {/* Render the section content (expandable/collapsible) */}
            {isOpen && (
                <div className={styles.menu_item_list}>
                    <ul
                        className={[
                            isMobile
                                ? styles.mobile_menu_section
                                : styles.menu_section,
                        ].join(' ')} // Combine mapped style classes
                    >
                        {props.list.map((item, index) => (
                            <MenuItem item={item} key={`item-${index}`} />
                        ))}
                    </ul>
                </div>
            )}
        </div>
    );
};

export default MenuSection;
