import * as styles from "../../../styles/restaurant.module.css";
import { useSelector } from "react-redux";
import React from "react";
import SocialMediaIcon from "../../icons";

const RestaurantMedia = () => {
    const restaurant = useSelector((state) => state.restaurant);

    const socialMediaPlatforms = ["maps", "facebook", "twitter", "instagram"];

    return (
        <div className={styles.restaurant_home_media}>
            {
                restaurant.restaurantInfo
                    .filter(([name, link]) =>
                        socialMediaPlatforms.includes(name) &&
                        typeof link === "string" &&
                        link.trim()
                    )
                    .map(([name, link], index) => (
                        <a
                            key={index}
                            className={styles.restaurant_home_item}
                            href={link}
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <SocialMediaIcon app={name} />
                        </a>
                    ))
            }
        </div>
    );
};

export default RestaurantMedia;
