import * as styles from "../../../styles/restaurant.module.css";
import React, { useState } from 'react';
import MenuSection from './MenuSection';
import { useSelector } from 'react-redux';
import MediaQuery from 'react-responsive';

const ViewSortedMenuList = (props) => {
    const [openedSectionIndex, setOpenedSectionIndex] = useState(null); // State to track which section is open
    const restaurant = useSelector((state) => state.restaurant);

    const handleSectionToggle = (index) => {
        // Toggle the section open/close based on the clicked index
        setOpenedSectionIndex(openedSectionIndex === index ? null : index);
    };

    return (
        <div className={styles.current_section_list}>
            <MediaQuery maxWidth={767}>
                {restaurant.menus[restaurant.openedMenuIndex].map((individualSectionArray, index) => {
                    const currentSection = individualSectionArray[0].section;
                    return (
                        <div className={styles.mobile_sortedMenuList} key={index}>
                            <MenuSection
                                className={styles.mobile_menu_block}
                                sectionTitle={currentSection}
                                sectionIndex={index}
                                list={individualSectionArray}
                                isOpen={openedSectionIndex === index} // Pass open/close status
                                onToggle={handleSectionToggle} // Pass toggle function to MenuSection
                                {...props}
                            />
                        </div>
                    );
                })}
            </MediaQuery>

            <MediaQuery minWidth={768} maxWidth={991}>
                {restaurant.menus[restaurant.openedMenuIndex].map((individualSectionArray, index) => {
                    const currentSection = individualSectionArray[0].section;
                    return (
                        <div className={styles.mobile_sortedMenuList} key={index}>
                            <MenuSection
                                className={styles.menu_block}
                                sectionTitle={currentSection}
                                sectionIndex={index}
                                list={individualSectionArray}
                                isOpen={openedSectionIndex === index} // Pass open/close status
                                onToggle={handleSectionToggle} // Pass toggle function to MenuSection
                                {...props}
                            />
                        </div>
                    );
                })}
            </MediaQuery>

            <MediaQuery minWidth={992}>
                {restaurant.menus[restaurant.openedMenuIndex].map((individualSectionArray, index) => {
                    const currentSection = individualSectionArray[0].section;
                    return (
                        <div className={styles.sortedMenuList} key={index}>
                            <MenuSection
                                className={styles.menu_block}
                                sectionTitle={currentSection}
                                sectionIndex={index}
                                list={individualSectionArray}
                                isOpen={openedSectionIndex === index} // Pass open/close status
                                onToggle={handleSectionToggle} // Pass toggle function to MenuSection
                                {...props}
                            />
                        </div>
                    );
                })}
            </MediaQuery>
        </div>
    );
};

export default ViewSortedMenuList;
